import { request } from '../utils/http';

export function GetOddsListFirstId(query) {
  return request({ url: 'api/getoddslistfirstid', method: 'post', data: query });
}
export function SaveOddsInfo(data) {
  return request({ url: 'api/saveoddsinfo', method: 'post', data: data });
}



