<template>
  <div>
    <mu-appbar style="width: 100%;" color="primary">
      <mu-button icon slot="left" @click="openHome">
        <mu-icon size="22" value="home"></mu-icon>
      </mu-button>
      {{ title }}
      <mu-button v-show="hasFilter" flat slot="right" @click="doShowFilter">过滤</mu-button>
      <mu-button flat slot="right" @click="showMenu = !showMenu">菜单
      </mu-button>
    </mu-appbar>

    <mu-drawer :open.sync="showMenu" :docked="docked" :right="position === 'right'">
      <mu-list>
        <mu-list-item avatar button :ripple="false">
          <mu-list-item-action @click="openMyCenter">
            <mu-avatar>
              <img v-if="sysUserInfo.PortraitUrl" :src="sysUserInfo.PortraitUrl">
              <img v-else  src="../assets/img/avatar/avatar01.jpg">
            </mu-avatar>
          </mu-list-item-action>
          <mu-list-item-title> {{ sysUserInfo.UserName }}</mu-list-item-title>
        </mu-list-item>
      </mu-list>

      <template v-for="item in MemuList">
        <mu-expansion-panel>
          <div slot="header">
            <mu-icon size="36" :value="item.icon"></mu-icon>
            {{ item.title }}
          </div>
          <mu-divider></mu-divider>
          <div class="menu_wrap">
            <template v-for="subItem in item.SubMenuList">
              <div class="menu_item" @click="goLink(subItem.UrlFor)" >
                <mu-icon color="#35F" size="36" :value="subItem.icon"></mu-icon>
                <div class="menu_item_title">{{subItem.title}}</div>
              </div>

            </template>

          </div>
          <mu-divider></mu-divider>
        </mu-expansion-panel>
      </template>
<br>
      <mu-button color="primary" @click="goLogout">登出</mu-button>

    </mu-drawer>

    <mu-bottom-sheet v-if="hasFilter" :open.sync="showFilter" close="doCloseFilter" >
      <div class="demo-chip">
        <template v-for="item in FilterItemsCur">
          <div v-if="item.type==='select'">
            <mu-select v-model="item.Value" full-width>
              <span slot="prepend">{{item.label}}</span>
              <mu-option v-for="option, index in item.options" :key="option.v" :label="option.n" :value="option.v"></mu-option>
            </mu-select>
          </div>
          <div v-else-if="item.type ==='date'">
            <mu-date-input v-model="item.Value"  :placeholder="item.placeholder" :icon="item.icon"  label-float full-width no-display>
              <span slot="prepend">{{item.label}}</span>
            </mu-date-input>
          </div>
          <div v-else>
            <mu-text-field :full-width="true" v-model="item.Value" :type="item.type" :placeholder="item.placeholder" :icon="item.icon">
              <span slot="prepend">{{item.label}}</span>
            </mu-text-field>
          </div>


        </template>
        <mu-flex justify-content="between"  class="demo-chip">
          <mu-button color="primary" @click="closeFilter">取消</mu-button>
          <mu-button color="primary" @click="doOK">确定</mu-button>
        </mu-flex>
      </div>
    </mu-bottom-sheet>
  </div>
</template>

<script>
import {mapState} from "vuex";

export default {
name: "LoHeader",
  components: {
  },
  props:{
    title:{
      type:String,
      default:"标题文字"
    },
    FilterItems:{
      type:[Array,Object] ,
       default:()=>{
         // let a = [
         // {label:'类型:',Value:'aa1',type:"select",placeholder:"类型",icon:"",options:[{n:'a',v:1},{n:'b',v:2}]},
         // {label:'时间:',Value:'',type:"date",placeholder:"时间",icon:""},
         // {label:'用户名:',Value:'aa3',type:"text",placeholder:"用户名",icon:""},
         // {label:'用户名:',Value:'343',type:"number",placeholder:"用户名",icon:""},];
         // return a
       },
    }

  },
  data() {
    return{
      hasFilter:false,
      showFilter:false,
      showMenu:false,
      width: '80%',
      height: '100%',

      docked: false,
      position: 'right',
      MemuList:[
        {title:'财务管理',icon:'',MenuType:1,UrlFor:'',
          SubMenuList:[
            {title:'充值申请管理',icon:'home',MenuType:2,UrlFor:'SaveMoneyApplyList'},
            {title:'提现申请管理',icon:'home',MenuType:2,UrlFor:'DrawMoneyApplyList'},
            {title:'充值列表',icon:'home',MenuType:2,UrlFor:'SaveMoneyList'},
            {title:'提现列表',icon:'home',MenuType:2,UrlFor:'DrawMoneyList'},
            {title:'流水查询',icon:'home',MenuType:2,UrlFor:'AccountList'},
            {title:'充值申请管理',icon:'home',MenuType:2,UrlFor:'SaveMoneyApplyList'},
            {title:'充值申请管理',icon:'home',MenuType:2,UrlFor:'SaveMoneyApplyList'},
          ]},
        {title:'用户管理',icon:'',MenuType:1,UrlFor:'',
          SubMenuList:[
            {title:'用户列表',icon:'home',MenuType:2,UrlFor:'GameUserList'},
          ]},
        {title:'报表',icon:'',MenuType:1,UrlFor:'',
          SubMenuList:[
            {title:'主投注记录',icon:'home',MenuType:2,UrlFor:'GroupBetList'},
          ]},
        {title:'开奖结果',icon:'',MenuType:1,UrlFor:'',
          SubMenuList:[
            {title:'加拿大28开奖结果',icon:'home',MenuType:2,UrlFor:'Zg28JndAwardList'},
            {title:'北京28开奖结果',icon:'home',MenuType:2,UrlFor:'Zg28BjAwardList'},
            {title:'新加坡28开奖结果',icon:'home',MenuType:2,UrlFor:'Zg28XjpAwardList'},
          ]},
      ],

      FilterItemsCur:[
      ],
      FilterItemsOld:[
      ],
    }
  },
  mounted() {
  if (!this.FilterItems) {
    return
  }
    if (this.FilterItems.length>0) {
      this.hasFilter = true
      this.FilterItemsCur = JSON.parse(JSON.stringify(this.FilterItems))
      this.FilterItemsOld = JSON.parse(JSON.stringify(this.FilterItems))
    }
  },
  computed: {
    ...mapState({
      sysUserInfo: state => state.sysUser.sysUserInfo
    })
  },
  methods: {
     defaultFilterItems(){

    },
    goLogout(){
      this.$store.dispatch('sysUser/logout','')
        .then(() => {
          this.$router.push({ path: this.redirect || '/login'});
        })
        .catch((e) => {
        })
    },
    goLink(u) {
      console.log(u);
      this.$router.push({ path: this.redirect || u});
    },
    openHome(){
      this.$router.push({ path: this.redirect || '/home'});
    },
    openMyCenter(){
      this.$router.push({ path: this.redirect || '/MyCenter'});
    },
    closeFilter(){
      this.showFilter= false;
    },
    doShowFilter(){
      for (let i=0;i<this.FilterItemsCur.length;i++) {
        if (this.FilterItemsCur[i].Value!=this.FilterItemsOld[i].Value) {
          this.FilterItemsCur[i].Value = this.FilterItemsOld[i].Value;
        }
      }
      this.showFilter= true;
    },
    doOK(){
      let isQuery = false;
      for (let i=0;i<this.FilterItemsCur.length;i++) {
        if (this.FilterItemsCur[i].Value!=this.FilterItemsOld[i].Value) {
          isQuery = true;
          this.FilterItemsOld[i].Value = this.FilterItemsCur[i].Value;
        }
      }

      this.showFilter= false;
      if (isQuery) {
        console.log("aaaa");
        this.$emit("query",this.FilterItemsCur)
      }
    },
    doCloseFilter(){

    }
  }
}
</script>

<style scoped>
.menu_wrap {
  display: flex;
  flex-wrap: wrap;
}

.menu_item {
  width: 50%;
  text-align: center;
  padding: 18px 10px;
  box-sizing: border-box;
  font-size: 14px;
  line-height: 25px;
  font-size: 14px;
  border-right: 1px solid #f0f0f0;
  border-bottom: 1px solid #f0f0f0;
}

.menu_item_title {
  color: black;
  /*font-weight: 800;*/
  font-size: 16px;
}
</style>



